@indicate-counter: 3;

.carousel {
  width: 100vw;
  height: 50vh;
  padding: 0 20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  .left-arrow {
    border: 4px solid grey;
    border-right: none;
    border-bottom: none;
    transform: rotate(-45deg);
    border-color: rgb(187, 182, 182);
    box-shadow: grey;
    width: 1rem;
    height: 1rem;
  }
  .right-arrow {
    border: 4px solid grey;
    border-left: none;
    border-top: none;
    transform: rotate(-45deg);
    border-color: rgb(187, 182, 182);
    box-shadow: grey;
    width: 1rem;
    height: 1rem;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: center;
    .title {

    }
    .img {
      width: 100%;
    }
    .navigator {
      display: flex;
      gap: 1rem;
      justify-content: center;
      .generate-circle(@n, @i: 1) when (@i <= @n){
        .circle() {
          width: 0.25rem;
          height: 0.25rem;
          border-radius: 50%;
          background:rgb(187, 182, 182);
        }
        .circle-@{i} {
          .circle();
        }
        .generate-circle(@n, @i + 1);
      }
      .generate-circle(@indicate-counter);
    }
  }
}